

    button {
        
        height: 60px;
        width: 300px;
        background: #6f2c6f;
        color: white;
        font-size: 1em;
        cursor: pointer;
        text-shadow: 0 0 5px rgba(0,0,0,0.4);
        transition: all ease 1.6s;
        span {
            transition: all ease .4s;
        }
        &:hover {
            transition: all ease .6s;
            background: #deafde;
            
            
        }
        &:disabled {
            background: grey;
            color: white;
            cursor: default;
            &:hover {
                letter-spacing: inherit;
            }
        }
    }
    button.secondary {
        background: #2c6f52;
        &:hover {
            background: #235841;
            color: #eeffdd;
        }
    }

    
.alright {
    letter-spacing: 1px;
}
.wide {
    letter-spacing: 3px;
    font-size: 1.2em;
    transition: all ease .4s;
    color: #484848;
    text-shadow: rgba(0, 0, 0, 0.55) 2px 3px 6px;
    background: transparent;
    text-shadow: inherit;
}